import $http from '../../untils/request.js'
export default{
	name: 'songSheet',
	onLoad(option) {
		this.type = option.type
		this.id = option.id
		if(option.type == 1){
			$http('top/list?idx=' + option.id).then(res => {
				console.log(res)
				this.data = res.data.playlist
				this.creator = res.data.playlist.creator
				this.privileges = res.data.privileges
				this.scriCount()
				this.bg = `background-image: url(${this.creator.backgroundUrl})`
			})
		}
		if(option.type == 0){
			//排行榜详情
			$http('playlist/detail?id=' + option.id).then(res => {
				console.log(res)
				this.data = res.data.playlist
				this.creator = res.data.playlist.creator
				this.privileges = res.data.privileges
				this.scriCount()
				this.bg = `background-image: url(${this.creator.backgroundUrl})`
			})
		}
	},
	methods:{
		scriCount(){
			let scr = this.data.subscribedCount/10000 + ''
			this.subscribedCount = Number(scr.split('.')[0]) 
		},
		jumpMV(id){
			console.log(id)
			uni.navigateTo({
				url:'../mv/index?id=' + id + '&type=0',
				success() {
					console.log("跳转mv页面")
				},
				fail() {
					console.log('跳转mv页面失败')
				}
			})
		},
		jumpSong(i, arr){
			let type = this.type
			let lastid = this.id
			uni.navigateTo({
				url: '../playSong/index?index=' + i + '&type=' + type + '&lastId=' + lastid,
				success() {
					console.log("跳转播放页面")
				},
				fail() {
					console.log('跳转播放页面失败')
				}
			})
		}
	},
	data(){
		return{
			data: '',
			subscribedCount: '',
			creator: '',
			bg: '',
			type: '',
			id: ''
		}
	}
}
